<template>
  <div class="music-list" ref="list">
    <base-nav-bar class="nav-bar" ref="navBar">
      <template #nav-left>
        <div class="back" @click="$router.back()"><i class="icon-back"></i></div>
      </template>
      <template #nav-center>
        <h1 class="title">{{name}}</h1>
      </template>
    </base-nav-bar>
    <div class="page-top" ref="pageTop">
      <div class="filter"></div>
    </div>
    <div class="page-bottom" ref="pageBottom">
      <scroll
        class="scroll"
        ref="scroll"
        :dataArr="songLists"
        :probeType="3"
        :isListenScroll="true"
        :click="true"
        @scroll="scrollHandle"
        :pullUpLoad="true"
        @pullingUp="pullingUpHandle"
      >
        <song-list :song-list="songLists" @song-click="songClickHandle" :rank="rank"/>
      </scroll>
      <div class="song-list-bg" ref="bg"></div>
      <loading v-show="!songLists.length" class="loading"/>
    </div>
    <song-list-player-button
      :songList="songLists"
      class="plyBtn"
      @play-all="playAll"
    />
  </div>
</template>
<script>
import BaseNavBar from 'components/common/BaseNavBar/BaseNavBar.vue';
import Scroll from 'components/common/scroll/Scroll.vue';
import SongList from 'components/content/SongList.vue';
import Loading from 'components/common/loading/Loading.vue';
import SongListPlayerButton from 'components/content/SongListPlayerButton.vue';
import { createNamespacedHelpers } from 'vuex';
import setBottom from 'mixin/scaleDomHeight';

const {
  mapActions,
} = createNamespacedHelpers('songModule');
const TOP_IMG_EXPEND_ZINDEX = 10;
const TOP_IMG_RESET_ZINDEX = 1;
export default {
  components: {
    BaseNavBar,
    Scroll,
    SongList,
    Loading,
    SongListPlayerButton,
  },
  props: {
    songLists: {
      type: Array,
      default() {
        return [];
      },
    },
    name: {
      type: String,
      default: '暂无',
    },
    icon: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      maxTranslateDistance: 0,
      offset: 0,
    };
  },
  mixins: [setBottom],
  mounted() {
    // 上半部分高度
    this.topHeight = this.$refs.pageTop.offsetHeight;
    // navBar的高度，也是top的最小高度
    this.minTopHeight = this.$refs.navBar.$el.offsetHeight;
    // 设置下班页面高度
    this.$refs.pageBottom.style.top = `${this.topHeight}px`;
    this.$refs.pageTop.style.backgroundImage = `url(${this.icon})`;
    this.maxTranslateDistance = this.topHeight - this.minTopHeight;
  },
  methods: {
    scrollHandle(position) {
      const { y } = position;
      if (y < 0) {
        const moveDistance = Math.min(this.maxTranslateDistance, -y);
        this.$refs.bg.style.transform = `translateY(-${moveDistance}px)`;
        if (moveDistance === this.maxTranslateDistance) {
          this._changeTopHeight();
        } else {
          this._changeTopHeight(true);
        }
      } else {
        this._expandTop(y);
      }
    },
    pullingUpHandle() {
      this.$emit('pullingUp');
    },
    songClickHandle(event) {
      // 判斷是否點擊歌曲更多信息按鈕
      let target = event.target.closest('.more');
      if (target) {
        return;
      }
      // 判斷是否點擊歌曲選項
      target = event.target.closest('.song-list-item');
      if (!target) {
        return;
      }
      const { index } = target.dataset;
      this.changeCurrentSong({
        songList: [this.songLists[index]],
      });
    },
    finishPullUp() {
      this.$refs.scroll.finishPullUp();
    },
    playAll() {
      this.changeSongList(this.songLists);
    },
    handlePlayList() {
      if (this.playList.length === 0) {
        this.$refs.list.style.bottom = '0px';
      } else {
        this.$refs.list.style.bottom = `${this.miniPlayerHeight}px`;
      }
    },
    _changeTopHeight(reset = false) {
      if (reset) {
        this.$refs.pageTop.style.paddingTop = '70%';
        this.$refs.pageTop.style.zIndex = TOP_IMG_RESET_ZINDEX;
        return;
      }
      this.$refs.pageTop.style.paddingTop = `${this.minTopHeight}px`;
      this.$refs.pageTop.style.zIndex = TOP_IMG_EXPEND_ZINDEX;
    },
    _expandTop(y) {
      this.$refs.pageTop.style.zIndex = TOP_IMG_EXPEND_ZINDEX;
      const percent = Math.abs(y / this.topHeight);
      const scale = 1 + percent;
      this.$refs.pageTop.style.transform = `scale(${scale})`;
      if (scale === 1) {
        this.$refs.pageTop.style.zIndex = TOP_IMG_RESET_ZINDEX;
      }
    },
    ...mapActions([
      'changeCurrentSong',
      'changeSongList',
    ]),
  },
  computed: {
    rank() {
      return this.$route.name === 'rankDetail';
    },
  },
  watch: {
    fullScreen(nVal, oVal) {
      if (nVal === oVal || nVal) {
        return;
      }
      const bottom = this.playList.length ? this.miniPlayerHeight : 0;
      this.$refs.pageBottom.style.bottom = `${bottom}px`;
    },
  },
  emits: ['pullingUp'],
};
</script>
