<template>
  <ul class="song-list" @click="$emit('song-click', $event)">
    <li
      v-for="(item, index) in songList"
      :key="index"
      class="song-list-item"
      :data-index="index"
    >
      <div class="rank" v-show="rank" :class="getRankClass(index)">
        <p>{{getRankContent(index)}}</p>
      </div>
      <div class="content">
        <h2 class="name">{{item.name}}</h2>
        <p class="desc">{{getDesc(item)}}</p>
      </div>
      <div class="buttons">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-bofang"></use>
        </svg>
        <svg class="icon more" aria-hidden="true">
          <use xlink:href="#icon-gengduomore10"></use>
        </svg>
      </div>
    </li>
  </ul>
</template>
<script>
export default {
  props: {
    songList: {
      type: Array,
      required: true,
    },
    rank: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    getDesc(item) {
      const author = item.singerName.reduce((pre, cur) => `${pre}/${cur}`);
      return `${author}·${item.albumName}`;
    },
    getRankClass(index) {
      if (index < 3) {
        return `icon icon${index}`;
      }
      return 'text';
    },
    getRankContent(index) {
      if (index < 3) {
        return '';
      }
      return index + 1;
    },
  },
};
</script>
