import getSongPlayInfo from 'api/song/song1';

class Song {
  constructor({
    id,
    albumId,
    albumName,
    singerName,
    name,
    maxBr,
  }) {
    this.id = id;
    this.albumId = albumId;
    this.albumName = albumName;
    this.singerName = singerName;
    this.name = name;
    this.enable = false;
    this.url = '';
    this.maxBr = maxBr;
    this.lyric = '';
    this.icon = '';
    this.songTime = 0;
    this.loved = false;
  }

  async getPlayInfo() {
    try {
      const res = await getSongPlayInfo(this.id, this.maxBr, this.albumId);
      ({
        url: this.url,
        lyric: this.lyric,
        picUrl: this.icon,
      } = res);
      this._getSongTime();
      this.enable = true;
      return Promise.resolve();
    } catch (err) {
      return Promise.reject(err);
    }
  }

  _getSongTime() {
    const endIndex = this.lyric.lastIndexOf(']');
    const startIndex = this.lyric.lastIndexOf('[') + 1;
    const timeString = this.lyric.slice(startIndex, endIndex);
    const minutes = parseInt(timeString.substr(0, 2), 10);
    const seconds = parseInt(timeString.substr(3, 2), 10);
    const milliseconds = parseInt(timeString.substr(6, 3), 10);
    const totalMilliseconds = minutes * 60 * 1000 + seconds * 1000 + milliseconds;
    this.songTime = totalMilliseconds;
    this.minutes = minutes;
    this.seconds = seconds;
    this.milliseconds = milliseconds;
  }
}

export default Song;
