import request from 'assets/js/network';
import Song from 'assets/js/class/Song';

function extractSongInfo(item) {
  let maxBr = 0;
  if (item.privilege) {
    maxBr = item.privilege.maxbr;
  } else if (item.h) {
    maxBr = item.h.br;
  } else if (item.m) {
    maxBr = item.m.br;
  } else {
    maxBr = item.l.br;
  }
  return {
    id: item.id,
    albumId: item.al.id,
    albumName: item.al.name,
    singerName: item.ar.map((val) => val.name),
    name: item.name,
    maxBr,
  };
}

async function getSingerSongList(options) {
  try {
    const res = await request({
      url: '/artist/songs',
      params: {
        order: 'hot',
        offset: options.offset || 0,
        limit: options.limit || 30,
        id: options.id,
      },
    });
    const { more } = res;
    const songList = res.songs.map((item) => new Song(extractSongInfo(item)));
    return { songList, more };
  } catch (err) {
    return Promise.reject(err);
  }
}

async function getRecommendSongList(id) {
  try {
    const res = await request({
      url: '/playlist/detail',
      params: {
        id,
      },
    });
    return {
      songList: res.playlist.tracks.map((item) => new Song(extractSongInfo(item))),
      songIdList: res.playlist.trackIds.map((item) => item.id),
    };
  } catch (err) {
    return Promise.reject(err);
  }
}

async function getSongDetail(ids) {
  try {
    const res = await request({
      url: '/song/detail/',
      params: {
        ids,
      },
    });
    const arr = res.songs.map((item, index) => {
      const temp = item;
      temp.privilege = res.privileges[index];
      return extractSongInfo(temp);
    });
    return arr;
  } catch (err) {
    return Promise.reject(err);
  }
}

async function getSongPlayInfo(id, br, albumId) {
  try {
    const { url } = (await request({
      url: '/song/url',
      params: {
        id,
        br,
      },
    })).data[0];
    const { lyric } = (await request({
      url: '/lyric',
      params: {
        id,
      },
    })).lrc;
    const { picUrl } = (await request({
      url: '/album',
      params: {
        id: albumId,
      },
    })).album;
    return {
      url,
      lyric,
      picUrl,
    };
  } catch (err) {
    return Promise.reject(err);
  }
}

export {
  getSingerSongList,
  getSongPlayInfo,
  getRecommendSongList,
  getSongDetail,
  extractSongInfo,
};
