import request from 'assets/js/network';

async function getSongPlayInfo(id, br, albumId) {
  try {
    const { url } = (await request({
      url: '/song/url',
      params: {
        id,
        br,
      },
    })).data[0];
    const { lyric } = (await request({
      url: '/lyric',
      params: {
        id,
      },
    })).lrc;
    const { picUrl } = (await request({
      url: '/album',
      params: {
        id: albumId,
      },
    })).album;
    return {
      url,
      lyric,
      picUrl,
    };
  } catch (err) {
    return Promise.reject(err);
  }
}

export default getSongPlayInfo;
